import React from 'react';


const DropDownMenu = (props) => {
    const {hole, score, setScore, open, setOpen, stats, setStats, setPreShot, setPostShot, setHole, userToken, setUserToken} = props;
    
    const handleNewRound = () => {
        setOpen(!open);
        setPreShot(false);
        setPostShot(false);
        setStats(false);
        setHole(0);
        setScore(null);
    }
   
    const handleStats = () => {
        setOpen(!open)
        setStats(!stats);
    }
    const handleHome = () => {
        setStats(false);
        setPreShot(false);
        setPostShot(false);
        setHole(0);
        setOpen(!open)
    }   
    const handleLogOut = () => {
        setUserToken('');
        setOpen(!open);
        setPreShot(false);
        setPostShot(false);
        setStats(false);
        setHole(0);
        setScore(null);
    }
    
    return (
        <ul className="menu"> 
             { userToken &&
                <li className="menu-item">
                    <button onClick={handleLogOut}>Log Out</button>
                </li>
             }
             <li className="menu-item">
                <button onClick={handleHome}>Home</button>
            </li>
            <li className="menu-item">
                <button onClick={handleNewRound}>New Round</button>
            </li>
            <li className="menu-item">
                <button onClick={handleStats}>Stats</button>
            </li>
        </ul> 
    )
}

export default DropDownMenu;